// app/contact/page.js
"use client";
import { motion } from "framer-motion";
import Layout from "@/components/Layout";
import { 
  EnvelopeIcon, 
  PhoneIcon, 
  MapPinIcon,
  LinkedinIcon,
  GithubIcon 
} from "lucide-react";
import { SiFiverr } from "react-icons/si";

export default function ContactPage() {
  return (
    <Layout>
      <motion.section
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ duration: 0.8 }}
        className="min-h-screen py-20 px-8 md:px-16 flex items-center justify-center"
      >
        <div className="max-w-4xl w-full">
          {/* Header */}
          <motion.div
            initial={{ y: -20 }}
            animate={{ y: 0 }}
            className="text-center mb-16"
          >
            <h1 className="text-4xl md:text-6xl font-extrabold mb-6">
              Contact <span className="bg-cyan-300 bg-clip-text text-transparent">Me</span>
            </h1>
            <div className="w-24 h-1 bg-cyan-300 mx-auto mb-8"></div>
            <p className="text-gray-300 max-w-2xl mx-auto">
              Let's connect! Here's how you can reach me.
            </p>
          </motion.div>

          {/* Contact Card */}
          <motion.div
            initial={{ scale: 0.95 }}
            animate={{ scale: 1 }}
            className="bg-gray-800/50 backdrop-blur-lg rounded-2xl p-8 md:p-12 shadow-2xl shadow-cyan-300/10 border border-gray-700 max-w-2xl mx-auto"
          >
            <div className="grid md:grid-cols-2 gap-12">
              {/* Contact Info */}
              <div className="space-y-8">
                {/* Phone */}
                <motion.div 
                  whileHover={{ x: 5 }}
                  className="flex items-start gap-4"
                >
                  <div className="p-3 bg-cyan-300/10 rounded-full flex-shrink-0">
                    <PhoneIcon className="text-cyan-300 w-5 h-5" />
                  </div>
                  <div>
                    <h3 className="text-gray-400 text-sm">Phone</h3>
                    <a 
                      href="tel:+923304576247" 
                      className="text-white hover:text-cyan-300 transition text-lg"
                    >
                      +92 304 576247
                    </a>
                  </div>
                </motion.div>

                {/* Email */}
                <motion.div 
                  whileHover={{ x: 5 }}
                  className="flex items-start gap-4"
                >
                  <div className="p-3 bg-cyan-300/10 rounded-full flex-shrink-0">
                    <EnvelopeIcon className="text-cyan-300 w-5 h-5" />
                  </div>
                  <div>
                    <h3 className="text-gray-400 text-sm">Email</h3>
                    <a 
                      href="mailto:mustafaprogrammer786@gmail.com" 
                      className="text-white hover:text-cyan-300 transition text-lg"
                    >
                      mustafaprogrammer786@gmail.com
                    </a>
                  </div>
                </motion.div>

                {/* Location */}
                <motion.div 
                  whileHover={{ x: 5 }}
                  className="flex items-start gap-4"
                >
                  <div className="p-3 bg-cyan-300/10 rounded-full flex-shrink-0">
                    <MapPinIcon className="text-cyan-300 w-5 h-5" />
                  </div>
                  <div>
                    <h3 className="text-gray-400 text-sm">Location</h3>
                    <p className="text-white text-lg">Shakargarh, Punjab, Pakistan</p>
                  </div>
                </motion.div>
              </div>

              {/* Social Links */}
              <div className="flex flex-col">
                <h3 className="text-xl font-semibold mb-6 text-cyan-300">Connect With Me</h3>
                <div className="grid grid-cols-2 gap-4">
                  {/* LinkedIn */}
                  <motion.a
                    whileHover={{ y: -3, scale: 1.05 }}
                    whileTap={{ scale: 0.95 }}
                    href="https://linkedin.com/in/yourprofile"
                    target="_blank"
                    rel="noopener noreferrer"
                    className="flex items-center gap-3 p-4 bg-gray-700 hover:bg-cyan-300/10 rounded-xl border border-gray-600 hover:border-cyan-300 transition"
                  >
                    <LinkedinIcon className="text-cyan-300 w-5 h-5 flex-shrink-0" />
                    <span className="text-white">LinkedIn</span>
                  </motion.a>

                  {/* GitHub */}
                  <motion.a
                    whileHover={{ y: -3, scale: 1.05 }}
                    whileTap={{ scale: 0.95 }}
                    href="https://github.com/yourusername"
                    target="_blank"
                    rel="noopener noreferrer"
                    className="flex items-center gap-3 p-4 bg-gray-700 hover:bg-cyan-300/10 rounded-xl border border-gray-600 hover:border-cyan-300 transition"
                  >
                    <GithubIcon className="text-cyan-300 w-5 h-5 flex-shrink-0" />
                    <span className="text-white">GitHub</span>
                  </motion.a>

                  {/* Fiverr */}
                  <motion.a
                    whileHover={{ y: -3, scale: 1.05 }}
                    whileTap={{ scale: 0.95 }}
                    href="https://fiverr.com/yourprofile"
                    target="_blank"
                    rel="noopener noreferrer"
                    className="flex items-center gap-3 p-4 bg-gray-700 hover:bg-cyan-300/10 rounded-xl border border-gray-600 hover:border-cyan-300 transition"
                  >
                    <SiFiverr className="text-cyan-300 text-xl flex-shrink-0" />
                    <span className="text-white">Fiverr</span>
                  </motion.a>

                  {/* Email */}
                  <motion.a
                    whileHover={{ y: -3, scale: 1.05 }}
                    whileTap={{ scale: 0.95 }}
                    href="mailto:mustafaprogrammer786@gmail.com"
                    className="flex items-center gap-3 p-4 bg-gray-700 hover:bg-cyan-300/10 rounded-xl border border-gray-600 hover:border-cyan-300 transition"
                  >
                    <EnvelopeIcon className="text-cyan-300 w-5 h-5 flex-shrink-0" />
                    <span className="text-white">Email</span>
                  </motion.a>
                </div>
              </div>
            </div>
          </motion.div>
        </div>
      </motion.section>
    </Layout>
  );
}